<template>
  <div>
    <h3 style="background: rgba(255, 255, 255, 0.9); margin-top: 2vh">
      测评：新品推送、日常宣发
    </h3>
    <div class="main">
      <span style="border-right: black 1px solid">
        <div style="position: relative; height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">多选新品：</div>
          <div
            style="
              float: left;
              width: 35%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul>
              <li
                @click="newBackgroundClick(index)"
                class="newProductLi"
                v-for="(item, index) in multipleNewProductList"
                :key="index"
              >
                {{ item.sku_name }}
              </li>
            </ul>
          </div>

          <i
            @click="addShop()"
            class="el-icon-circle-plus-outline"
            style="
              z-index: 9;
              cursor: pointer;
              position: absolute;
              font-size: 4rem;
              left: 70%;
              top: 80%;
            "
          ></i>
        </div>
        <div style="position: relative; height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">多选老产品：</div>
          <div
            style="
              float: left;
              width: 35%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul>
              <li
                @click="OldBackgroundClick(index)"
                class="oldProductLi"
                v-for="(item, index) in multipleOldProductList"
                :key="index"
              >
                {{ item.product_name }}
              </li>
            </ul>
          </div>
          <i
            @click="addEmail"
            class="el-icon-circle-plus-outline"
            style="
              z-index: 9;
              cursor: pointer;
              position: absolute;
              font-size: 4rem;
              left: 70%;
              top: 125%;
            "
          ></i>
        </div>
        <div style="height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">多选发送邮箱：</div>
          <div
            style="
              float: left;
              width: 35%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul>
              <li
                @click="emailBackgroundClick(index)"
                class="allEmail"
                v-for="(item, index) in multipleSendEmailList"
                :key="index"
              >
                {{ item.email }}
              </li>
            </ul>
          </div>
        </div>
      </span>
      <span>
        <div style="height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">推送商品列表：</div>
          <div
            style="
              float: left;
              width: 35%;
              height: 100%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul class="shopList">
              <li
                v-for="(item, index) in pushProductList"
                :key="index"
                class="sendShop"
              >
                {{ item }}
              </li>
            </ul>
          </div>
        </div>
        <div style="height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">选择客户：</div>
          <div
            style="
              float: left;
              width: 35%;
              height: 100%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul>
              <li
                v-for="(item, index) in selectClientList"
                :key="index"
                class="sendEmail"
              >
                {{ item }}
              </li>
            </ul>
          </div>
        </div>
      </span>
      <span>
        <div style="height: 33%; padding: 2vh">
          <div style="float: left; width: 25%">选择国家：</div>
          <div
            style="
              float: left;
              width: 35%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <ul>
              <li v-for="(item, index) in countryList" :key="index">
                {{ item.country }}
              </li>
            </ul>
          </div>
        </div>
        <div style="height: 33%">
          <div style="float: left; width: 25%">邮件推送文案：</div>
          <div
            style="
              float: left;
              width: 35%;
              max-height: 100%;
              overflow-y: auto;
              border: 2px solid #dddddd;
            "
          >
            <div></div>
            <el-input
              type="textarea"
              placeholder="请输入内容"
              v-model="emailSendInput"
              maxlength="999"
              show-word-limit
              style="width: 100%"
              :rows="10"
            >
            </el-input>
          </div>
        </div>
      </span>
    </div>
    <el-button
      type="primary"
      @click="submitAll()"
      style="position: relative; left: 60%; top: -20vh; width: 10vw"
      >一 键 推 送</el-button
    >
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      multipleNewProductList: [], //多选新品列表
      multipleOldProductList: [], //多选老产品列表
      multipleSendEmailList: [], //多选发送邮箱
      pushProductList: [], //推送商品列表
      selectClientList: [], //推送客户列表
      countryList: [], //选择国家
      emailSendInput: "", //推送文案
    };
  },
  methods: {
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/product_delivery",
        method: "get",
      }).then((res) => {
        this.countryList = res.data.country_data;
        this.multipleNewProductList = res.data.new_product_data;
        this.multipleOldProductList = res.data.old_product_data;
        this.multipleSendEmailList = res.data.shop_data;
        console.log(this.countryList, "国家列表");
        console.log(this.multipleNewProductList, "新品");
        console.log(this.multipleOldProductList, "旧品");
        console.log(this.multipleSendEmailList, "邮箱");
        console.log(this.selectClientList, "客户");
      });
    },
    newBackgroundClick(index) {
      //改变多选新品里LI背景颜色
      let newPdc = document.querySelectorAll(".newProductLi");
      if (newPdc[index].style.background == "skyblue") {
        newPdc[index].style.background = "";
      } else {
        newPdc[index].style.background = "skyblue";
      }
    },
    OldBackgroundClick(index) {
      //改变多选老产品里LI背景颜色
      let oldPdc = document.querySelectorAll(".oldProductLi");
      if (oldPdc[index].style.background == "skyblue") {
        oldPdc[index].style.background = "";
      } else {
        oldPdc[index].style.background = "skyblue";
      }
    },
    emailBackgroundClick(index) {
      //改变多选发送邮箱背景颜色
      let emailLi = document.querySelectorAll(".allEmail");
      if (emailLi[index].style.background == "skyblue") {
        emailLi[index].style.background = "";
      } else {
        emailLi[index].style.background = "skyblue";
      }
    },

    addShop() {
      //添加新老产品
      let newPdc = document.querySelectorAll(".newProductLi");
      let oldPdc = document.querySelectorAll(".oldProductLi");
      let demo = [];
      for (let i = 0; i < newPdc.length; i++) {
        if (newPdc[i].style.background == "skyblue") {
          demo.push(newPdc[i].innerHTML);
        }
      }
      for (let i = 0; i < oldPdc.length; i++) {
        if (oldPdc[i].style.background == "skyblue") {
          demo.push(oldPdc[i].innerHTML);
        }
      }
      this.pushProductList = demo;
    },

    addEmail() {
      //添加发送邮箱
      let emailLi = document.querySelectorAll(".allEmail");
      let demo = [];
      for (let i = 0; i < emailLi.length; i++) {
        if (emailLi[i].style.background == "skyblue") {
          demo.push(emailLi[i].innerHTML);
        }
      }
      this.selectClientList = demo;
    },

    submitAll() {
      //一键发送
      let sendShop = document.querySelectorAll(".sendShop");
      let sendEmail = document.querySelectorAll(".sendEmail");
      let demo = [];
      let demo2 = [];
      for (let i = 0; i < sendShop.length; i++) {
        demo.push(sendShop[i].innerHTML);
      }
      for (let i = 0; i < sendEmail.length; i++) {
        demo2.push(sendEmail[i].innerHTML);
      }
      if (demo == "" || demo2 == "" || this.emailSendInput == "") {
        alert("商品列表、客户、推送文案都不能为空！");
      } else {
        this.$axios
          .post(this._url2 + "server/customer_management/product_delivery", {
            params: {
              product_name: demo,
              customer_email: demo2,
              push_copy: this.emailSendInput,
            },
          })
          .then((res) => {
            if (res.data.code == 200) {
              alert("发送成功");
            }
          });
      }
    },
  },
  created() {
    this.getDefaultData();
  },
};
</script>

<style scoped>
.main {
  background: rgba(255, 255, 255, 0.9);
  height: 80.8vh;
}
.main span {
  display: inline-block;
  width: 33%;
  height: 80vh;
}

ul li {
  font-size: 0.8rem;
}

.newProductLi,
.oldProductLi,
.allEmail {
  cursor: pointer;
}
</style>


